//    -- set kardan dadeh avalie az jomle full name va pass ro khahim dasht inja
import React, {memo, useState, useEffect} from 'react';
// trasnlation library
import {useTranslation} from "react-i18next";
// library jquery
import $ from 'jquery';
// library materilize bary estefade az componenthai materilize
import M from 'materialize-css';
// library redux baray connect action - reducer - store
import {useDispatch, connect, useSelector} from "react-redux";

// action setData for sabt dadeh karbar
import {SetData as NewData} from '../../../../entity/Redux/Auth/SignUp/Action';
// secret code barary save datahai avali karbar dar localstorage
import {SECRET_USER_DATA} from "../../../../services/Authentication/Types";

// functional component asli setData
const UserDataNew = memo((props) => {
    const {t, i18n} = useTranslation(); // translate functional
    const dispatch = useDispatch(); // make object baray useDispatch
    const resLog = useSelector(state => state.Auth).resLog;
    console.log("res log" , resLog);
    // bad az ejray component ejra mishavad
    useEffect(() => {
        if (resLog) {
            //   zamani ke password set shavad in bakhsh barai direct be set username ajra mishavad
            if (resLog.data.statusCode === 200) {
                // save kardan etelat karbar da storage jahat estefadeh dar componenthai badi
                localStorage.setItem(SECRET_USER_DATA, JSON.stringify(resLog.data));
                setTimeout(() => {
                    window.location.pathname = '/upload-file'; // redirect ro upload-file
                }, 1200)

            }
            // end
        }

        // materilize select component
        var elems = document.querySelectorAll('select');
        M.FormSelect.init(elems);
    })


    {/*
    -- language
    -- do zabaneh kardan site
    */
    }

    function handleLang(e) {
        // e.preventDefault();
        // sabt type language dar storage
        if (!localStorage.getItem("lan")) {
            localStorage.setItem("lan", 'en');
        }
        //daryaft type language as localstorage
        var lan = localStorage.getItem("lan");
        var ckeckbox = $("input[name=cahngelan]");
        if (ckeckbox.is(":checked") && lan === 'en') {
            localStorage.setItem("lan", 'ch');
        } else {
            localStorage.setItem("lan", 'en');
        }
        let lang = localStorage.getItem('lan');
        // gharar dadan language dar library translate
        i18n.changeLanguage(lang);
    }

// end language


    // state baray sabt dadeh doctor
    const [dataUser, setDataUser] = useState({});


    {/*
        -- set Data
        -- set kardan dadehhai karbar dar state
    */
    }

    function setData(e) {
        setDataUser({
            ...dataUser,
            [e.target.name]: e.target.value
        })
    }


// end setData


    {/*
        -- submit data user
        -- dispatch function newdata baraye sabt etelaat karbar
    */
    }

    function submitUserDetail(e) {
        e.preventDefault();
        dispatch(NewData(dataUser));
    }

    // end submit data user

    const select_type = t('select-type.l');
    return (
        <div className="auth_back">
            <div id="back_shado"></div>

            <div className="container">
                <div className="col s12">
                    <div className="row">
                        <div className="col l4 m2 s1">

                        </div>
                        <div className="col l4 m8 s10">

                            <div id="auth_box">
                                <div id="change_or_sub">
                                    <input type="checkbox" name="cahngelan" onChange={handleLang}
                                           id="change_lang_login"/>
                                    <span>{t('Lang.l')}</span>
                                    {/*<span></span>*/}
                                </div>

                                <div className={`mini_box_over`}>
                                    <div id={`mini_circule`}>
                                        <i className="material-icons">person_outline</i>
                                    </div>
                                </div>
                                <div className={`mini_box_over`} style={{right: 0}}>
                                    <div id={`mini_circule_r`}>
                                        <img src="/img/icons/doctorB.png"/>
                                    </div>
                                </div>
                                <div className="container">
                                    <div className="row">

                                        <div className="row">
                                            <div className="input-field col s12" style={{marginTop: 55}}>
                                                <p id="text_sub">{t('Welcome-to.l')}</p>
                                                <p id="text_sub" style={{
                                                    fontSize: "40px",
                                                    marginTop: "15px !important",
                                                    marginBottom: "15px !important"
                                                }}>Bi Con</p>
                                                <p id="text_sub">{t('UserRegData.l')}</p>
                                            </div>
                                        </div>

                                        <div className="register_type_user" id="verifyBorder" style={{hight: "auto"}}>


                                            <div id="loading_back">


                                                <div className="preloader-wrapper big active" id="loading_progress">
                                                    <div className="spinner-layer spinner-blue-only">
                                                        <div className="circle-clipper left">
                                                            <div className="circle"></div>
                                                        </div>
                                                        <div className="gap-patch">
                                                            <div className="circle"></div>
                                                        </div>
                                                        <div className="circle-clipper right">
                                                            <div className="circle"></div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>

                                            {/*
                                                -- form data
                                                -- form atelat avalie karbar
                                            */}


                                            <form onSubmit={e => submitUserDetail(e)}>

                                                <div className="container">
                                                    <div className="row">
                                                        <div className="col s12">
                                                            <div className="row">

                                                            </div>


                                                            {/*
                                                                input marbout be fullname doctor
                                                            */}
                                                            <div className="input-field col s12" style={{marginTop: 0}}>
                                                                <p style={{color: '#ccc'}}>&nbsp;&nbsp;{t('UserName.l')}</p>
                                                                <div id="input_back">
                                                                    <input
                                                                        placeholder={t('UUserName.l')}
                                                                        autoComplete="false"
                                                                        name="fullname"
                                                                        onChange={e => setData(e)}
                                                                        required type="text" className="validate"/>
                                                                </div>
                                                                <p id="alert-p">&nbsp;&nbsp;{t('must-field.l')}</p>

                                                                {/*<label htmlFor="password">Password</label>*/}
                                                            </div>


                                                            {/*
                                                                input marbout be password doctor
                                                            */}
                                                            <div className="input-field col s12" style={{marginTop: 0}}>
                                                                <p style={{color: '#ccc'}}>&nbsp;&nbsp;{t('Password.l')}</p>
                                                                <div id="input_back">
                                                                    <input name="password"
                                                                           placeholder={t('URegPassword.l')}
                                                                           autoComplete="false"
                                                                           name="password"
                                                                           onChange={e => setData(e)}
                                                                           required type="password"
                                                                           className="validate"/>
                                                                </div>
                                                                <p id="alert-p">&nbsp;&nbsp;{t('must-field.l')}</p>
                                                            </div>


                                                            <div className={`col s12`}
                                                                 style={{marginTop: 15, marginBottom: 30}}>
                                                                <input className="btn blue darken-4 waves-effect"
                                                                       type="submit"
                                                                       id="check_phone_num" value={t('Save.l')}/>
                                                                {/*</Link>*/}
                                                            </div>
                                                        </div>

                                                    </div>
                                                </div>


                                            </form>

                                            {/*    end form data   */}

                                        </div>
                                    </div>
                                </div>

                            </div>


                        </div>
                        <div className="col l4 m2 s1">

                        </div>
                    </div>

                </div>

            </div>

        </div>
    );
})

// export kardan component
export default UserDataNew;